<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png" @click="skip" />
    <HelloWorld msg="Welcome fto Your Vue.js App" />
    <van-button type="default" @click="goto">默认按钮</van-button>
    <van-skeleton row="3" animate :loading="loading">
      <div class="dd">实际内容</div>
      <div>实际内容</div>
      <div>实际内容</div>
    </van-skeleton>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import { Button, Skeleton } from "vant";
export default {
  data() {
    return {
      loading: true
    };
  },
  name: "Home",
  components: {
    HelloWorld,
    [Button.name]: Button,
    [Skeleton.name]: Skeleton
  },
  mounted() {
    console.log(this.$route);
    let me = this;
    setTimeout(() => {
      me.loading = false;
    }, 3500);
  },
  methods: {
    skip() {
      this.$router.push("/about");
    },
    goto() {
      this.$router.push("/pay");
    }
  }
};
</script>
<style lang="scss">
.home {
  background: red;
  .dd {
    @include flex;
  }
}
</style>
